import {createAsyncThunk} from '@reduxjs/toolkit';

import API from 'api/API';
import {ICommentRequest} from 'types/ICommentRequest';

interface ICommentCreateArgs {
    id: string,
    data: ICommentRequest,
}

export const createCommentThunk = createAsyncThunk(
    'post/comment',
    async (args: ICommentCreateArgs, {rejectWithValue}) => {
        const result = await API.createComment(args.id, args.data);
        if (result.data) {
            return result.data;
        }
        return rejectWithValue(result.errorMessage);
    },
);
