import React from 'react';

import UserAvatar, {IUserAvatarProps} from 'ui/icon-buttons/UserAvatar/UserAvatar';

import './Comment.scss';

interface ICommentProps {
    authorFirstName: string,
    authorLastName: string,
    avatarProps: IUserAvatarProps,
    commentText: string,
}

export default function Comment(props: ICommentProps) {
    const {authorFirstName, authorLastName, avatarProps, commentText} = props;

    return (
        <div className='Comment'>
            <UserAvatar {...avatarProps} />
            <div className="Comment__body">
                <span className='Comment__author-info'>{authorFirstName + ' ' + authorLastName}</span>
                <span className='Comment__text'>
                    {commentText}
                </span>
            </div>
        </div>
    );
}
