import React from 'react';
import {useNavigate} from 'react-router-dom';

import {TitleLabelEnum} from 'enums/TitleLabelEnum';
import {TitleAssignmentEnum} from 'enums/TitleAssignmentEnum';
import {IComment} from 'types/IComment';
import Title from 'ui/Title';
import {IUserAvatarProps} from 'ui/icon-buttons/UserAvatar/UserAvatar';
import {IconSizeEnum} from 'enums/IconSizeEnum';
import {ROUTE_PROFILE} from 'constants/routes';
import Comment from './views/Comment';

import './Comments.scss';

interface ICommentsListProps {
    postComments: IComment[],
}

export default function Comments(props: ICommentsListProps) {
    const {postComments} = props;
    const navigate = useNavigate();

    const AVATAR_PROPS: IUserAvatarProps = {
        size: IconSizeEnum.BIG,
        onAvatarClick: () => navigate(ROUTE_PROFILE),
    };

    return (
        <div className='Comments'>
            <Title
                label={TitleLabelEnum.COMMENTS}
                assignment={TitleAssignmentEnum.SECTION}
            />
            {
                postComments.length > 0 && (
                    <div className="Comments__list">
                        {postComments.map((comment) => (
                            <Comment
                                key={comment.id}
                                authorFirstName={comment.user.firstName}
                                authorLastName={comment.user.lastName}
                                avatarProps={AVATAR_PROPS}
                                commentText={comment.text}
                            />
                        ))}
                    </div>
                )
            }
        </div>
    );
}
