import React, {useEffect, useState} from 'react';
import {useDispatch} from 'react-redux';
import {FormProvider, SubmitHandler, useForm} from 'react-hook-form';
import {yupResolver} from '@hookform/resolvers/yup';

import Button from 'ui/form/Button';
import TextField from 'ui/form/inputs/TextField';
import {createCommentSchema} from 'constants/createCommentSchema';
import {ICommentRequest} from 'types/ICommentRequest';
import Form from 'shared/Form';
import {ButtonTypeEnum} from 'enums/ButtonTypeEnum';
import {ButtonVariantEnum} from 'enums/ButtonVariantEnum';
import {ButtonSizeEnum} from 'enums/ButtonSizeEnum';
import {TDispatch} from 'store/store';
import {createCommentThunk} from 'store/thunks/createCommentThunk';
import {IComment} from 'types/IComment';

interface ICommentCreateFormProps {
    postId: string,
    addCommentCb: (comment: IComment) => void,
}

export default function CommentCreateForm(props: ICommentCreateFormProps) {
    const {postId, addCommentCb} = props;
    const [isDisabled, setIsDisabled] = useState(true);
    const [buttonVariant, setButtonVariant] = useState<ButtonVariantEnum>(ButtonVariantEnum.DISABLED);
    const dispatch: TDispatch = useDispatch();
    const methods = useForm({
        resolver: yupResolver<ICommentRequest>(createCommentSchema),
    });

    const text = methods.watch('text');

    useEffect(() => {
        if (text) {
            setIsDisabled(false);
            setButtonVariant(ButtonVariantEnum.PRIMARY);
        } else {
            setIsDisabled(true);
            setButtonVariant(ButtonVariantEnum.DISABLED);
        }
    }, [text]);

    const onCreateComment: SubmitHandler<ICommentRequest> = async (data) => {
        const formData = new FormData();
        formData.append('text', data.text);
        try {
            const createCommentArgs = {
                id: postId,
                data: formData as unknown as ICommentRequest,
            };
            const newComment = await dispatch(createCommentThunk(createCommentArgs)).unwrap();
            addCommentCb(newComment);
            methods.reset();
        } catch (error: unknown) {
            console.error('Create comment error: ', error);
        }
    };

    return (
        <FormProvider {...methods}>
            <Form onSubmit={methods.handleSubmit(onCreateComment as SubmitHandler<any>)}>
                <TextField
                    key='text'
                    name='text'
                    placeholder=" "
                    label=" "
                    id="text"
                />
                <Button
                    key='submit'
                    text=' '
                    name='submit'
                    type={ButtonTypeEnum.SUBMIT}
                    variant={buttonVariant}
                    size={ButtonSizeEnum.BIG}
                    disabled={isDisabled}
                />
            </Form>
        </FormProvider>
    );
}
