import React from 'react';

import {ButtonTypeEnum} from 'enums/ButtonTypeEnum';
import {ReactComponent as IconComponent} from 'icons/Subscribe.svg';

import './SubscribeIcon.scss';

export interface ISubscribeIconProps {
    onSubscribe: () => void,
}

export default function SubscribeIcon(props: ISubscribeIconProps) {
    const {onSubscribe} = props;

    return (
        <button
            type={ButtonTypeEnum.BUTTON}
            className="SubscribeIcon"
            onClick={onSubscribe}
            aria-label='Subscribe icon'
        >
            <IconComponent />
        </button>
    );
}
