import React, {useEffect, useMemo, useState} from 'react';
import {useNavigate} from 'react-router-dom';
import {useDispatch, useSelector} from 'react-redux';

import {IComment} from 'types/IComment';
import {IPost} from 'types/IPost';
import {getPostByIdThunk} from 'store/thunks/getPostByIdThunk';
import getFromLocalStorage from 'utils/getFromLocalStorage';
import {TDispatch, TStore} from 'store/store';
import ReturnButton from 'ui/form/icon-buttons/ReturnButton';
import Loader from 'ui/Loader';
import PostCard, {IPostCardProps} from 'shared/PostCard/PostCard';
import UserCard, {IUserCardProps} from 'shared/UserCard/UserCard';
import Comments from './views/Comments';
import CommentCreateForm from './views/PostCommentCreateForm';

import './PostPage.scss';

export default function PostPage() {
    const selectedPostId: string = getFromLocalStorage('selectedPostId');
    const prevLocation: string = getFromLocalStorage('prevLocation');
    const {user} = useSelector((state: TStore) => state.init);
    const navigate = useNavigate();
    const dispatch: TDispatch = useDispatch();
    const [currentPost, setCurrentPost] = useState<IPost>();
    const [isSubscribeIcon, setIsSubscribeIcon] = useState<boolean>();
    const [comments, setComments] = useState<IComment[] | []>([]);

    const sortCommentsByTime = (array: IComment[]) => array.sort((a, b) => new Date(b.createTime).getTime() - new Date(a.createTime).getTime());

    const addComment = (comment: IComment) => {
        setComments(() => [comment, ...comments]);
    };

    const getCurrentPost = async () => {
        try {
            if (!currentPost) {
                const selectedPost = await dispatch(getPostByIdThunk(selectedPostId)).unwrap();
                setCurrentPost(selectedPost);
                const sortComments = sortCommentsByTime(selectedPost.comments);
                setComments(sortComments || []);
                setIsSubscribeIcon(selectedPost?.creatorId !== user?.id);
            }
        } catch (error) {
            console.error(error);
        }
    };

    useEffect(() => {
        getCurrentPost();
    }, []);

    const onSubscribe = () => {};

    const POSTCARD_PROPS = useMemo(() => ({
        post: currentPost,
        isFullMode: false,
        isActionBar: false,
    }), [currentPost]);

    // const USERCARD_PROPS = useMemo(() => ({
    //     firstName: currentPost?.creator.firstName,
    //     lastName: currentPost?.creator.lastName,
    //     email: currentPost?.creator.email,
    //     isActionBar: false,
    //     isSubscribeIcon,
    //     onSubscribe,
    // }), [currentPost, isSubscribeIcon]);

    const onReturnClick = () => {
        navigate(prevLocation);
    };

    return (
        <div className='PostPage'>
            <div className="PostPage__wrapper">
                {
                    !currentPost
                        ? <Loader />
                        : (
                            <>
                                <ReturnButton onReturnClick={onReturnClick} />
                                <PostCard {...POSTCARD_PROPS as IPostCardProps} />
                                {/* <UserCard {...USERCARD_PROPS as IUserCardProps} /> */}
                                <Comments postComments={comments ?? []} />
                                <CommentCreateForm
                                    postId={currentPost.id.toString()}
                                    addCommentCb={addComment}
                                />
                            </>
                        )
                }
            </div>
        </div>
    );
}
