import * as yup from 'yup';

import {EMAIL_PATTERN} from 'constants/patterns';

export const editUserSchema = yup.object().shape({
    firstName: yup.string().required(' ').optional(),
    lastName: yup.string().required(' ').optional(),
    email: yup.string().required(' ').matches(EMAIL_PATTERN, ' email').optional(),
    file: yup
        .mixed<FileList>()
        .test('fileFormat', ' ', (value) => {
            if (!value || value.length === 0) {
                return true;
            }
            const file = value[0];
            return file && ['image/jpg', 'image/jpeg', 'image/png', 'image/gif', 'image/svg', 'image/svg+xml'].includes(file.type);
        })
        .optional(),
    description: yup.string().optional(),
});
