import * as yup from 'yup';

import {EMAIL_PATTERN} from 'constants/patterns';

export const registrationSchema = yup.object().shape({
    firstName: yup.string().required(' ').optional(),
    lastName: yup.string().required(' ').optional(),
    email: yup.string().required(' ').matches(EMAIL_PATTERN, ' email'),
    password: yup.string().required(' ').min(4, ' 4 '),
    description: yup.string().optional(),
});
